* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  position: sticky;
  top: 0;
  background: var(--bg);
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  overflow: hidden;
}
.navbar-logo {
  flex: 1;
  padding-left: 5rem;
  height: inherit;
  display: flex;
  align-items: center;
}
@media (max-width: 599px) {
  .navbar-logo {
    padding-left: 2rem;
  }
}
.navbar-links {
  flex: 1;
  padding-right: 5rem;
  height: inherit;
}
.navbar-links-list {
  height: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-links-list a:hover {
  color: var(--secondary-colour);
}

.burger {
  height: inherit;
  width: 2rem;
  margin-right: 5rem;
  display: flex;
  align-items: center;
}
@media (max-width: 599px) {
  .burger {
    margin-right: 2rem;
  }
}
.burger-container {
  height: 2rem;
  width: inherit;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  cursor: pointer;
}
.burger-container span {
  width: 2rem;
  height: 2px;
  background: var(--primary-colour);
}

html {
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 3.5rem;
}

a {
  text-decoration: none;
  font-size: 1.5rem;
  color: var(--primary-colour);
  font-weight: 700;
  transition: all 0.3s ease-in-out;
}
@media (max-width: 599px) {
  a {
    font-size: 1.2rem;
  }
}

li {
  list-style: none;
}

:root {
  --bg: #fff;
  --primary-colour: #29b58a;
  --secondary-colour: #b52954;
}

@media (max-width: 1023px) {
  .hide-for-mobile {
    position: absolute;
    top: 100%;
    left: 100%;
    width: 30vw;
    height: 50vh;
    padding: 0;
  }
  .hide-for-mobile ul {
    height: inherit;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }
}

@media (min-width: 1024px) {
  .hide-for-desktop {
    display: none;
  }
}